* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

h1 {
  color: #fff;
  text-align: center;
  background-color: #03cc65;
  padding-top: 30px;
}

h3 {
  color: #001a2e;
  text-align: center;
  background-color: #03cc65;
}

p {
  text-align: center;
  margin: 50px 0;
  padding: 0 30px;
}

#progress {
  height: 64px;
  width: 64px;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: grid;
  position: fixed;
  bottom: 20px;
  right: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

#progress-value {
  height: calc(100% - 15px);
  width: calc(100% - 15px);
  background-color: #4f2ee8;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: grid;
}

.scroll-icon {
  fill: #fff;
}

